import React, { FunctionComponent } from 'react'
import './Todo.css'
import { Severity, TodoProps } from '../types'

interface CircleProps {
    color: string
}

const Circle: FunctionComponent<CircleProps> = ({ color }) => {

    return <div className="circle" style={{ backgroundColor: color }}></div>
}

export const Todo: FunctionComponent<TodoProps> = ({ title, severity = Severity.Low }) => {

    return (
        <div className="todo">
            <Circle color={severity} />
           Task: {title}
        </div>
    )
}