import React from 'react';
import logo from './logo.svg';
import './App.css';
import { Todo } from './components/Todo';
import { Severity, TodoProps } from './components/types';

const todos: TodoProps[] = [
  {
    title: 'Take out the trash',
    severity: Severity.Medium
  },
  {
    title: 'See Despicable Me 2...again'
  }
]

function App() {
  return (
    <div className="App">
      <div className="container">
        {/* The canvas */}

        {todos.map((todo, i) => <Todo key={i} {...todo} />)}

        <hr />
        {/* <Todo title="Take out the trash" severity={Severity.Medium} />
        <Todo title="See Despicable Me 2...again" /> */}
        <Todo title="Drink Tea" />
        <Todo title="Pet Jer" severity={Severity.Extreme} />
        <Todo title="I did it" />
        <Todo title="woot woooooot" />
        <Todo title="Clean my laundry" severity={Severity.High} />



        {/* End of canvas */}
      </div>
    </div>
  );
}

export default App;
